.employer_name  .team-field-content {
  color: var(--blue);
}

.team-field-content {
  line-height: 1.4;
}


#team-wrapper {
  margin: 20px auto;
  max-width: 1440px;
  padding-top: 34px;
  width: calc(100% - 2.5%);

  & .filter-controls {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    width: 100%;

    @media (width >=840px) {
      flex-direction: row;
    }

    & .filter-item {
      &:nth-child(1) {
        display: none;
        order: 25;
      }
      &:nth-child(2) {
        order: 4;
      }
      &:nth-child(3) {
        order: 2;
      }
      &:nth-child(4) {
        order: 1;
      }
      &:nth-child(5) {
        order: 3;
      }
      &:nth-child(6) {
        order: 5;
      }
    }
  }


  & .ats-button {
    background-color: transparent!important;
    font-family: 'museo_sans700';
    font-size: 14px;
    text-transform: uppercase;
    transition: color 123ms ease-in-out;

    &:hover {
      color: gray !important;
    }
  }

  & .ats-button.ats-button-active {
    color: var(--blue)!important;
  }
}

.ats-employer-panel-clean .ats-employer-panel-head {
  font-family: 'museo_sans500' !important;
}
